import React, { useEffect, useState, useCallback, useContext, ReactElement } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';

import styles from '../css/profile.module.css';
import { GET } from '../utils/requests';
import { Player } from '../models';
import { LoginContext } from '../App';
import { capitalise } from '../utils/prototype';
import { flags } from '../config.json';
import countries from '../assets/countries.json';
import contactFields from '../resources/contact';

export default function Team(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
}): ReactElement | null {

	const [player, setPlayer] = useState(null as any as Player);
	const updateProfile = useCallback(() => GET({ url: '/team/' + props.id })
		.then((p) => setPlayer(new Player(p, p.histories.length, p.setting))), [setPlayer, props.id]);
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	const loggedIn = useContext(LoginContext);

	if (!player) return null;

	const total = (Math.max(player.histories.length, 1) - 1 - (player.isPlaying ? 1 : 0)).toString();
	const totalGames = player.histories.reduce((acc, curr) => {
		if (!curr) return acc;
		acc += curr.played;
		return acc;
	}, 0);
	const scores = !player ? {} : {
		Score: player.score.toString() + ' / ' + total,
		'Game score': player.gamePoints.toString() + ' / ' + totalGames.toString(),
		'Expected score': player.expectedScore.toFixed(2),
		'Expected game score': player.expectedGameScore.toFixed(2) + ' / ' + totalGames,
	};
	if (!player.settings.displayPoints) {
		delete scores['Game score'];
		delete scores['Expected game score'];
	}

	return <>
		<div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<div className={['container', styles.info].join(' ')}>
					<h1><span className='bold'>
						{player.name}
					</span></h1>
					{player.nationality ? <div className={styles.flag}>
						<img src={flags.replace('|', player.nationality)} alt={player.nationality}/>
						{countries[player.nationality as keyof typeof countries]}
					</div> : null}
				</div>
				<div className={['container', styles.info].join(' ')}>
					<h1>Contact</h1>
					{
						!loggedIn ?
							<Link to='/login'>Sign in to view contact details</Link> :
							!player.contact || !Object.values(player.contact).length ?
								<div>No contact details registered</div> :
								<div className={styles.grid}>
									{Object.entries(contactFields).map(([k, meta], i) => {
										let v = meta.value(player);
										if (!v) return null;
										if (typeof v === 'string' && v.startsWith('http')) v = v.split('/').pop();
										if (!meta.link) {
											return <div id={k} className={['button', 'link'].join(' ')} key={[k, i].join('i')}>
												{meta.icon[0] === 'img' ?
													<img src={meta.icon[1] as string} alt={k} /> :
													meta.icon[1]
												}
												{v}
											</div>
										} else {
											return <a
												id={k}
												href={meta.link.replace('|', v || '')}
												className={['button', 'link'].join(' ')}
												key={[k, i].join('i')}
												rel='noopener noreferrer'
												target='_blank'
											>
												{meta.icon[0] === 'img' ?
													<img src={meta.icon[1] as string} alt={k} /> :
													meta.icon[1]
												}
												{meta.name || capitalise(k)}
											</a>
										}
									})}
								</div>
					}
				</div>	
			</div>			
		</div>
	</>
}